  @media (min-width: 450px) {
    .main {
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 10px 10px; } }
  @media (min-width: 740px) {
    .main {
      grid-template-columns: repeat(3, 1fr);
      grid-gap: 10px 10px; } }
  @media (min-width: 1020px) {
    .main {
      grid-template-columns: repeat(4, 1fr);
      grid-gap: 20px 20px; } }

.score {
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
  overflow: visible; }

.error {
  color: #00695c; }

.correct {
  color: #aa66cc; }

.wrapper {
  width: 100%;
  max-width: 1020px;
  margin: 0 auto; }

.active {
  background: #00695c;
