/* Style buttons */
.btn {
  background-color: #80cbc4;
  border: none;
  color: white;
  padding: 12px 16px;
  font-size: 22px;
  cursor: pointer;
  transition: all 0.3s; }

.btn:hover {
  background-color: #00695c; }

.btn_close {
  background: rgba(0, 150, 136, 0);
  position: absolute;
  right: 0;
  top: 0; }

.btn-rotate {
  background: rgba(0, 150, 136, 0.4);
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1000; }

.game-btn {
  opacity: 1;
  transition: opacity linear 0.3s;
  border-style: solid;
  border-color: black; }

.repeat_btn {
  position: absolute;
  left: 45%;
  opacity: 1;
  transition: opacity linear 0.3s; }

.card {
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column; }
  @media (min-width: 320px) {
    .card {
      height: 300px; } }
  @media (min-width: 450px) {
    .card {
      height: 300px; } }
  @media (min-width: 740px) {
    .card {
      height: 280px; } }
  @media (min-width: 1020px) {
    .card {
      height: 250px; } }

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.4); }

.container {
  text-align: center;
  padding-top: 10%;
  width: 100%;
  height: 50px; }

.category-img {
  height: 100%;
  overflow: hidden; }

.flip-card {
  background-color: transparent;
  width: 100%;
  perspective: 1000px;
  overflow: hidden;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.4);
  transition: ease 0.3s; }
  @media (min-width: 320px) {
    .flip-card {
      height: 300px; } }
  @media (min-width: 450px) {
    .flip-card {
      height: 300px; } }
  @media (min-width: 740px) {
    .flip-card {
      height: 280px; } }
  @media (min-width: 1020px) {
    .flip-card {
      height: 250px; } }

.flip-card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2); }

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d; }

.flip-card.rotate .flip-card-inner {
  transform: rotateY(180deg); }

.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center; }

.flip-card-front {
  background-color: #80cbc4;
  color: #2d2d2d; }

.flip-card-back {
  background-color: #00695c;
  color: #b2dfdb;
  transform: rotateY(180deg); }

.word-container {
  height: 50px; }

.flip-card-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100; }

.card-img {
  overflow: hidden; }

.imgGame {
  overflow: hidden;
  transform: translateX(-10%);
  height: 100%;
  width: auto; }

.sidepanel {
  height: 100%;
  position: fixed;
  z-index: 1;
  width: 0;
  top: 0;
  left: 0;
  background-color: rgba(0, 150, 136, 0.7);
  overflow-x: hidden;
  padding-top: 60px;
  transition: all 0.5s; }

.sidemenu-item {
  cursor: pointer;
  padding: 8px 8px 8px 50px;
  text-decoration: none;
  font-size: 20px;
  font-weight: 700;
  display: block;
  transition: 0.3s;
  overflow: hidden;
  transition: all 0.2s; }

.sidemenu-item:hover {
  background: #00695c;
  color: #b2dfdb;
  transition: all 0.2s; }

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 100px;
  height: 34px; }

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0; }

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #b2dfdb;
  -webkit-transition: .4s;
  transition: .4s;
  color: rgba(255, 255, 255, 0); }

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s; }

input:checked + .slider {
  background-color: #00695c; }

input:checked + .slider {
  color: #b2dfdb; }

input:focus + .slider {
  box-shadow: 0 0 1px #00695c; }

input:checked + .slider:before {
  -webkit-transform: translateX(66px);
  -ms-transform: translateX(66px);
  transform: translateX(66px); }

/* Rounded sliders */
.slider.round {
  border-radius: 34px; }

.slider.round:before {
  border-radius: 50%; }

.on {
  line-height: 34px;
  padding-left: 5px;
  font-size: 1.5rem; }
